body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #eee;
	background-color: #4B644A;
}

a {
  text-decoration: none;
  color: #ADFCF9;
}

a:hover {
		text-decoration: underline;
	}

button {
	padding: 10px;
	border: 2px solid #666;
	border-radius: 6px;
	background: #ccc;
}

button:active {
		background: #aaa;
	}

button:disabled {
    color: #000;
  }

.refresh {
  min-width: 72px;
  padding: 2px 10px;
}

ul {
  margin: 0;
  padding: 0 0 0 1em;
}

li {
  list-style: none;
}

li.names-button,
	li.sanction-button {
		padding: 10px 0;
	}

table {
  border-collapse: collapse;
}

.persons {
  overflow: auto hidden;
  overscroll-behavior-x: contain;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
}

@media (prefers-reduced-motion: no-preference) {

.persons {
    scroll-behavior: smooth
}
  }

.persons {

  block-size: 100%;

  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 100%;
}

.persons .person {
    padding: 0 1em;
    scroll-snap-align: start;
    overflow-y: auto;
    overscroll-behavior-y: contain;
  }

.scores {
  width: 100%;
  overflow-x: auto;
}

.scores tr {
    background-color: #341C1C;
    color: #eee;
  }

.scores th, .scores td {
    padding: .5em;
    border-left: 1px solid #49393B;
    border-right: 1px solid #49393B;
    text-align: center;
  }

.scores th:first-child, .scores td:first-child {
      border-left: 0 none;
    }

.scores th:last-child, .scores td:last-child {
      border-right: 0 none;
    }

.scores tbody tr th {
      text-align: left;
    }

.scores tbody tr:nth-child(odd) {
      background-color: #49393B;
    }

.scores tbody tr:nth-child(odd) th, .scores tbody tr:nth-child(odd) td {
        border-color: #341C1C;
      }

.scores tbody tr:nth-child(even) {
      background-color: #341C1C;
    }

.scores tbody tr:nth-child(even) th, .scores tbody tr:nth-child(even) td {
        border-color: #49393B;
      }

@media (max-width: 600px) {
  table {
    width: 100%;
  }

  .scores {
    margin-left: -2em;
    width: calc(100% + 3em);
  }
}

